import React, { useState, useEffect } from 'react'
import {
	BrowserRouter as Router,
	Routes,
	Route,
	Link,
	useLocation,
} from 'react-router-dom'
import { Layout, Menu, ConfigProvider } from 'antd'
import { useTranslation } from 'react-i18next'
import Calendar from './components/Calendar'
import EventForm from './components/EventForm'
import Logo from './components/Logo'
import LanguageSwitcher from './components/LanguageSwitcher'
import { Provider } from 'react-redux'
import store from './store'
import enUS from 'antd/lib/locale/en_US'
import ruRU from 'antd/lib/locale/ru_RU'

const { Header, Content } = Layout

//         
const AppContent = () => {
	const location = useLocation()
	const [selectedKey, setSelectedKey] = useState('1')
	const { t, i18n } = useTranslation()
	const [locale, setLocale] = useState(i18n.language === 'ru' ? ruRU : enUS)

	useEffect(() => {
		setLocale(i18n.language === 'ru' ? ruRU : enUS)
	}, [i18n.language])

	useEffect(() => {
		if (location.pathname === '/') {
			setSelectedKey('1')
		} else if (location.pathname === '/new-event') {
			setSelectedKey('2')
		} else if (location.pathname.startsWith('/edit-event/')) {
			//        
			setSelectedKey('')
		}
	}, [location])

	return (
		<ConfigProvider locale={locale}>
			<Layout className='app-layout'>
				<Header>
					<Logo language={i18n.language} />
					<LanguageSwitcher />
					<Menu theme='dark' mode='horizontal' selectedKeys={[selectedKey]}>
						<Menu.Item key='1'>
							<Link to='/'>{t('calendar')}</Link>
						</Menu.Item>
						<Menu.Item key='2'>
							<Link to='/new-event'>{t('newEvent')}</Link>
						</Menu.Item>
					</Menu>
				</Header>
				<Content className='content-wrapper'>
					<div className='content-container'>
						<Routes>
							<Route path='/' element={<Calendar />} />
							<Route path='/new-event' element={<EventForm />} />
							<Route path='/edit-event/:id' element={<EventForm />} />
						</Routes>
					</div>
				</Content>
			</Layout>
		</ConfigProvider>
	)
}

function App() {
	return (
		<Provider store={store}>
			<Router
				basename={
					process.env.NODE_ENV === 'development' ? '/' : '/calendar-app'
				}
			>
				<AppContent />
			</Router>
		</Provider>
	)
}

export default App
